.timeline__list {
  padding: 1em 0 0 1.3em;
  margin: 0;
  list-style: none;
  position: relative;
  margin: 0 auto;
  max-width: 50rem;
}
.timeline__list::before {
  content: " ";
  height: 100%;
  width: 3px;
  background-color: #17b0a2;
  position: absolute;
  top: 0;
  left: 2.1em;
  z-index: -1;
}
.timeline li div {
  display: inline-block;
  vertical-align: top;
}
.timeline__date {
  width: 6rem;
  font-size: 1rem;
  text-align: center;
}
.timeline__feedback {
  width: 54%;
}
.timeline__feedback p {
  margin: 0;
  line-height: 1.3rem;
}
.timeline__item {
  margin: 2rem 0;
}
.timeline__bullet {
  text-align: center;
  padding-top: 0.25rem;
  font-weight: 800;
  margin-top: -0.4rem;
  color: white;
  width: 1.8em;
  height: 1.8em;
  box-sizing: border-box;
  border-radius: 50%;
  z-index: 1;
  margin-right: 0.5rem;
}
.timeline__bullet--red {
  border: 2px solid #db4f4f;
  background: #db5959;
}
.timeline__bullet--green {
  border: 2px solid #17b0a2;
  background: #17b0a2;
}
.timeline__bullet--orange {
  border: 2px solid #FF9800;
  background: orange;
}

