}
.schedule {
  margin-top: 1rem;
  text-align: center;
}
.schedule__timetable {
  display: grid;
  grid-template-areas: ". week" "time content";
  grid-template-columns: 64px;
  grid-template-rows: 45px;
  max-width: 40rem;
  min-width: 20rem;
  text-align: center;
  margin: 0 auto;
  margin-bottom: 2rem;
  padding: 1rem;
}
.schedule__timetable ul {
  margin: 0;
  padding: 0;
}
.schedule__item {
  list-style: none;
  border: 1px solid gray;
}
.schedule__item--checked {
  background-color: #17b0a2;
}
.schedule__week-names {
  grid-area: week;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  text-transform: uppercase;
}
.schedule__week-names > div {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}
.schedule__time-interval {
  grid-area: time;
  display: grid;
  grid-template-rows: repeat(15, 1fr);
}
.schedule__time-interval > div {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}
.schedule__board {
  grid-area: content;
  display: grid;
  grid-template-rows: repeat(15, 1fr);
  grid-template-columns: repeat(7, 1fr);
  border: 1px solid #cecece;
}

@media only screen and (min-width: 768px) {
  .schedule__timetable {
    width: 30rem;
  }
}
@media only screen and (min-width: 992px) {
  .schedule__timetable {
    width: 40rem;
  }
}
